    }

    #apppage .view3 {
        background-image: url(/static/media/rmap3.17ee4c91.jpg);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center center;
        height: 100vh;
    }
    
    #apppage h6 {
    line-height: 1.7;
    }

